<template>
	<view class="content">
		<view class="nav-top">
			<view class="logo-name">
				<view class="logo">
					<image src="../../static/images/home_active.png" class="img"></image>
				</view>
				<view class="name">LucKin Coffee</view>
			</view>
			<view class="index" @click="goindex">先逛一逛</view>
		</view>
		<view class="nav-content">
			<view class="welcome">找回密码！</view>
			<view class="please">Find password</view>
		</view>
		<view class="nav-login">
			<u-form :model="form" ref="uForm">
				<u-form-item label="邮箱" prop="number">
					<u-input placeholder='邮箱' class="id" v-model="form.number" />
				</u-form-item>
				<u-form-item label="验证码" prop="password">
					<u-input placeholder='验证码(6个字符)' v-model="form.password" type="number" />
					<button  class="codetime" @click="gocode" :disabled='isFalg'>{{this.isFalg==true?this.time:'发送验证码'}}</button>
				</u-form-item>
			</u-form>
			<view class="fogpass" @click="gologin">已有账号，立即登录</view>
			<view class="login" @click="next">下一步</view>
		</view>
		</view>
	</view>
	</view>
</template>

<script>
	import request from "../request/request.js"
	export default {
		data() {
			return {
				isFalg:false,
				time:60,
				num:60,
				timer:'',
				show: false,
				form: {
					number: '1367276692@qq.com',
					password: ''
				},
				rules: {
					number: [{
						required: true,
						pattern:  /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
						message: '请输入正确的邮箱',
						// trigger: ['blur', 'change']
					}],
					password: [{
						required: true,
						pattern: /^\d{6}$/,
						message: '验证码错误',
						// trigger: ['blur', 'change']
					}]
				}
			}
		},

		methods: {
			showPwd() {
				this.showPassword = !this.showPassword
			},
			goindex() {
				uni.switchTab({
					url: '../index/index'
				})
			},
			gologin(){
				uni.navigateTo({
					url:'../login/login'
				})
			},
			next() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						request({
							url:"/checkValidCode",
							method:"POST",
							data:{
								validCode:this.form.password
							},
							success:(res)=>{
								if(res.data.code=="K002"){
									uni.showToast({
										title:'验证码错误',
										icon:'none',
										mask:true,
										duration:1000
									})
								}else{
									console.log('验证成功')
									uni.navigateTo({
										url:"findpasstwo"
									})
								}
							}
						})
					} else {
						console.log('验证失败');
					}
				});
			},
			gocode(){
				this.isFalg=true
				this.timer=setInterval(()=>{
					this.tim()
				},1000)
				request({
					url:'/emailValidCode',
					method:"POST",
					data:{
						email:this.form.number
					},
					success:(res)=>{
						uni.showToast({
							title:"发送成功",
							icon:'none',
							mask:true,
							duration:1000
						})
					}
				})
			},
			tim(){
				this.time--
				if(this.time<10){
					this.time="0"+this.time
				}
				else{
					
				}
				if(this.time==0){
					clearInterval(this.timer)
					this.timer=null
					this.time=this.num
					this.isFalg=false
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		}
	}
</script>

<style lang="less" scoped>
	.content {
		.nav-top {
			margin: 10rpx 30rpx 10rpx 30rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.logo-name {
				display: flex;
				align-items: center;

				.logo {
					.img {
						width: 80rpx;
						height: 80rpx;
					}
				}

				.name {
					margin-left: 20rpx;
					font-weight: bold;
					color: #646566;
				}
			}

			.index {
				color: #0c34ba;
				font-weight: bold;
			}
		}

		.nav-content {
			margin: 30rpx;
			margin-top: 200rpx;

			.welcome {
				font-size: 60rpx;
				font-weight: bold;
				color: #646566;
			}

			.please {
				margin-top: 40rpx;
				font-size: 32rpx;
				color: #83838b;
			}
		}

		.nav-login {
			margin: 30rpx;

			.fogpass {
				margin-top: 50rpx;
				margin-left: 400rpx;
				color: #82848A;
			}

			.login {
				margin: 30rpx;
				margin-top: 100rpx;
				width: 600rpx;
				height: 100rpx;
				background-color: #0c34ba;
				color: #FFFFFF;
				text-align: center;
				line-height: 100rpx;
				border-radius: 50rpx;
			}
		}
	}

	::v-deep .u-form {
		margin: 80rpx 50rpx 30rpx 30rpx;
	}

	::v-deep .u-form-item--left__content__label {
		width: 100rpx;
	}

	::v-deep .uni-input-wrapper {
		margin-left:110rpx;
	}

	::v-deep .u-form-item__message {
		display: none;
	}
	::v-deep .u-input__right-icon{
		display: none;
	}
	::v-deep .codetime{
		width: 176rpx;
		text-align: center;
		color: #FFFFFF;
		border-radius: 6rpx;
		background-color: #0c34ba;
		font-size: 12rpx;
	}
</style>
